.custom-select {
    position: relative;
    
    width: 100%;
    max-width: 320px;
    
    font-family: sans-serif;

    font-family: $font-family--inter;
    font-size: 16px;
    line-height: 1.2;

    transition: border 0.3s;

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 100%;
        border: 4px solid transparent;
        border-radius: 16px;
        pointer-events: none;
        transform: translate3d(-50%, -50%, 0);
        transition: border 0.3s;
    }

    &.is-open {
        .custom-select__trigger-icon {
            opacity: 1;
            transform: rotate(180deg);
        }
        .custom-select__dropdown {
            opacity: 1;
            pointer-events: unset;
        }
    }
    &.is-empty {
        .custom-select__list::before {
            content: "  ";
            display: inline-block;
            padding: 8px;
            width: 100%;
        }
    }
    &.not-valid {
        .custom-select__trigger {
            border-color: $color-error;

            & > svg {
                color: $color-error;
            }
        }
        &.not-valid {
            .error {
                opacity: 1;
            }
        }
    }
    &.is-focused {
        &::before {
            border-color: #e3eeff;
        }
    }
    &.custom-select--disabled {
        pointer-events: none;

        .custom-select__trigger {
            color: #151d3f4d;

            .custom-select__trigger-icon {
                display: none;
            }
        }
    }
}

.custom-select__trigger {
    display: flex;
    align-items: center;
    padding: 19px 20px;

    border: 1px solid $color-light-gray;
    border-radius: 16px;
    background: #fff;

    cursor: pointer;

    transition: border 0.3s;

    svg {
        margin-right: 8px;
        flex-shrink: 0;
        width: 24px;
        height: 24px;

        transition: color 0.3s;
    }
}

.custom-select__trigger-input {
    position: relative;
    flex: 1;
    z-index: 0;
}

.custom-select__trigger-input input {
    border: none;
    outline: none;
    width: 100%;

    font-size: 16px;
    line-height: 1.2;
    background-color: transparent;

    &::placeholder {
        color: #151d3f4d;
    }
}
.custom-select__trigger-input-help {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;

    color: #151d3f4d;
    
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    pointer-events: none;
    z-index: 11;
}

.custom-select__trigger-icon {
    align-self: flex-end;
    flex-shrink: 0;
    width: 24px;
    height: 24px;

    opacity: 0.3;

    transition: transform 0.3s, opacity 0.3s;
    
    svg {
        margin: 0;
    }
}

.custom-select__dropdown {
    position: absolute;
    top: calc(100% + 8px);
    left: 0;
    right: 0;

    padding: 6px 4px;

    max-height: 220px;

    background: #fff;
    border: 1px solid $color-light-gray;
    border-radius: 12px;

    opacity: 0;
    pointer-events: none;
    overflow-y: auto;

    transition: opacity 0.3s;
    z-index: 2;

    &::-webkit-scrollbar {
        width: 4px;
        border-radius: 100px;
    }

    &::-webkit-scrollbar-track {
        background: transparent;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $color-gray;
        border-radius: 20px;
        border: 3px solid $color-gray;
    }
}

.custom-select__list {
    list-style: none;
    margin: 0;
    padding: 0;

    display: flex;
    flex-direction: column;
}

.custom-select__item {
    padding: 10px 12px;
    border-radius: 8px;
    cursor: pointer;

    transition: background 0.3s;

    @include hover-or-touch {
        background: #f0f6ff;
    }
    &:has(input:checked) {
        order: -1;
    }
}
