.market-info {

}
.market-info__header {
    position: relative;
    display: grid;
    align-items: flex-start;
    grid-template-columns: 1fr auto;
    grid-template-rows: fit-content fit-content;
    gap: 12px 24px;

    & > * {
        grid-column: 1 / 2;
    }

    @include vp--575 {
        grid-template-columns: 1fr;

        & > * {
            grid-column: unset;
        }
    }
}
.market-info__description {
    max-width: 1140px;

    font-family: $font-family--inter;

    color: $color-dark-gray;
}
.market-info__header-btns {
    display: flex;
    align-items: center;

    grid-column: 2 / 3;
    grid-row: 1 / -1;

    @include vp--575 {
        justify-content: flex-end;
        order: -1;
        grid-column: unset;
        grid-row: unset;
    }
}

.market-info__btn {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 48px;
    height: 48px;

    border-radius: 12px;

    background: #ECEDFF;
    border: 1px solid #ECEDFF;

    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);

    & + & {
        margin-left: 16px;
    }
}

.market-info__cities {
    margin-top: 20px;
    width: 100%;
    max-width: 340px;
}
.market-info__subtitle {
    display: flex;
    align-items: center;
    margin-bottom: 24px;

    font-family: $font-family--unbounded;
    font-weight: 500;
    font-size: 18px;

    svg {
        flex-shrink: 0;
        margin-right: 8px;
    }

    @include vp--767 {
        font-size: 16px;
    }
}
.market-info__content {
    margin-top: 20px;
    display: grid;
    grid-template-columns: 1fr 705px;
    gap: 20px;

    @include vp--1439 {
        grid-template-columns: 1fr 1fr;
    }
    @include vp--1023 {
        grid-template-columns: 1fr;
        gap: 12px;
    }
}
.market-info__activities {
    display: flex;
    flex-direction: column;
    grid-row: 1 / 3;

    padding: 24px;

    border: 1px solid $color-gray;
    border-radius: 24px;

    @include vp--1023 {
        grid-row: unset;
    }

    @include vp--374 {
        padding: 24px 12px;
    }
}

.market-info__activities-list {
}
.market-info__activities-item {
    padding: 22px 12px;

    display: flex;
    align-items: center;
    gap: 12px;

    font-family: $font-family--unbounded;
    font-size: 16px;

    &:not(:last-child) {
        border-bottom: 1px solid $color-light-gray;
    }

    svg {
        flex-shrink: 0;
        margin-left: auto;
        color: $color-green;
    }

    @include vp--767 {
        padding: 12px 0;
        font-size: 14px;
    }
}
.market-info__rating {
    .market-card {
        padding: 24px;
        max-width: 100%;

        .market-card__state {
            margin-top: 0;
        }
    }
    @include vp--1023 {
        order: -1;
    }
    @include vp--374 {
        .market-card {
            padding: 24px 12px;
        }
    }
}
.market-info__sources {
    grid-row: 2 / 3;


    align-items: flex-start;
    padding: 24px;
    gap: 24px;

    border: 1px solid $color-gray;
    border-radius: 24px;

    @include vp--1023 {
        order: 1;
        grid-row: unset;
    }
    @include vp--374 {
        padding: 24px 12px;
    }
}
.market-info__sources-list {
    display: flex;
    flex-direction: column;
}
.market-info__sources-item {
    display: flex;

    & + & {
        margin-top: 8px;
    }
}
.market-info__sources-link {
    display: flex;
    align-items: flex-start;

    padding: 12px 16px;

    width: 100%;

    background: rgba(234, 242, 255, 0.5);
    border-radius: 20px;

    font-family: $font-family--unbounded;
    font-size: 16px;
    line-height: 1.2;
    letter-spacing: -0.02em;

    transition: opacity 0.3s;

    span {
        max-width: 426px;
    }

    svg {
        flex-shrink: 0;
        margin-left: auto;
        color: #B2B7C5;
    }

    @include hover-or-touch {
        opacity: 0.5;
    }
    @include vp--767 {
        border-radius: 12px;
        font-size: 14px;
    }
}