.about-us {
    position: relative;
}
.about-us__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.about-us__pretitle {
    margin-bottom: 8px;
}
.about-us__title {
}
.about-us__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 20px;
    margin-top: 32px;
    padding: 32px;

    width: 100%;

    background: rgba(234, 242, 255, 0.3);
    border: 2px solid #FFFFFF;
    backdrop-filter: blur(45px);
    border-radius: 24px;

    @include vp--1023 {
        grid-template-columns: 1fr;
        padding: 16px;
        border-radius: 12px;
    }
}
.about-us__item {
    display: grid;
    grid-template-columns: 1fr 1fr 64px;
    justify-content: space-between;
    align-items: center;
    padding: 16px 0;
    gap: 0 20px;

    border-top: 1px solid $color-light-gray;

    &:nth-child(2),
    &:first-child {
        border-top: unset;
    }

    @include vp--1023 {
        width: 100%;
        grid-template-columns: 1fr 2fr 64px;

        &:first-child {
            border-top: unset;
        }
        &:nth-child(2) {
            border-top: 1px solid $color-light-gray;

        }
    }
    @include vp--767 {
        grid-template-columns: 1fr 24px;
        gap: 8px 20px;
        padding: 12px 0;
    }
}
.about-us__item-title {
    white-space: nowrap;
    @include vp--767 {
        grid-column: 1 / 2;
        grid-row: 1 / 2;
    }
}
.about-us__item-desc {
    @include vp--1279 {
        font-size: 14px;
    }
    @include vp--767 {
        grid-column: 1 / -1;
    }
}
.about-us__item-icon {
    width: 24px;
    height: 24px;

    color: #151D3F66;

    svg {
        flex-shrink: 0;
        width: 100%;
        height: 100%;
    }
    @include vp--767 {
        order: -1;
        grid-column: 2 / 3;
        grid-row: 1 / 2;
    }
}

.about-us__bg {
    position: absolute;
    

    &.about-us__bg--1 {
        left: 0;
        bottom: -338px;
    }
    &.about-us__bg--2 {
        right: 0;
        bottom: -80px;
    }

    @include vp--767 {
        &.about-us__bg--1 {
            bottom: unset;
            top: -138px;
        }
    }
    &.about-us__bg--2 {
        right: -48px;
        bottom: -56px;
    }
}