.company {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    padding: 24px;

    background: rgba(234, 242, 255, 0.3);
    border: 1px solid #E8F1FF;
    backdrop-filter: blur(45px);
    border-radius: 24px;

    font-family: $font-family--unbounded;
}
.company__name {
    margin-bottom: 32px;

    @include vp--767 {
        margin-bottom: 24px;
        font-size: 14px;
    }
}
.company__inn {
    margin-top: auto;

    color: $color-dark-gray-2;

    @include vp--767 {
        font-size: 14px;
    }
}
.company__fund {
    margin-top: 12px;
    font-weight: 600;
    font-size: 18px;

    @include vp--767 {
        font-size: 16px;
    }
}