.peculiarities {
    position: relative;
}
.peculiarities__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.peculiarities__pretitle {
    margin-bottom: 8px;
}
.peculiarities__title {
}
.peculiarities__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 20px;
    margin-top: 32px;

    @include vp--1023 {
        grid-template-columns: repeat(3, 1fr);
    }
    @include vp--767 {
        gap: 8px;
    }
    @include vp--575 {
        grid-template-columns: 1fr;
    }
}
.peculiarities__item {
    display: flex;
    flex-direction: column;

    padding: 24px;

    background: rgba(234, 242, 255, 0.3);
    border: 1px solid #E8F1FF;
    backdrop-filter: blur(45px);

    border-radius: 24px;

    @include vp--1023 {
        border-radius: 12px;
    }
}
.peculiarities__item-title {
    font-weight: 500;
    font-size: 20px;
    line-height: 1.25;

    @include vp--1279 {
        font-size: 18px;
    }
    @include vp--767 {
        font-size: 16px;
    }
}
.peculiarities__item-desc {
    margin-top: 12px;

    color: $color-dark-gray;

    @include vp--1279 {
        font-size: 14px;
    }
}
.peculiarities__item-icon {
    margin-bottom: 74px;
    width: 32px;
    height: 32px;

    svg {
        flex-shrink: 0;
        width: 100%;
        height: 100%;
    }

    @include vp--1279 {
        margin-bottom: 48px;
    }
    @include vp--767 {
        margin-bottom: 24px;
    }
    @include vp--575 {
        width: 24px;
        height: 24px;
    }
}