.indicators {

}
.indicators__title {
    text-align: center;
}

.indicators__select {
    margin-top: 20px;

    max-width: 450px;
}
.indicators__list {
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: center;
    gap: 20px;

    @include vp--1199 {
        grid-template-columns: repeat(2, 1fr);
    }
    @include vp--1023 {
        gap: 12px;
    }
    @include vp--767 {
        grid-template-columns: 1fr;
    }
}

.indicators__footer {
    display: grid;
    grid-template-columns: 1fr auto;
    align-items: center;
    gap: 12px;

    margin-top: 24px;

    padding: 32px;

    border: 1px solid #E8F1FF;
    border-radius: 32px;

    font-family: $font-family--unbounded;

    @include vp--767 {
        grid-template-columns: 1fr;
        padding: 24px;
        border-radius: 20px;
    }
}
.indicators__footer-title {
    font-weight: 500;
    font-size: 20px;
    line-height: 1.2;

    @include vp--767 {
        font-size: 18px;
    }
}
.indicators__footer-subtitle {
    color: $color-dark-gray;
}
.indicators__footer-link {
    grid-column: 2 / 3;
    grid-row: 1 / 3;

    height: fit-content;

    @include vp--767 {
        grid-column: unset;
        grid-row: unset;
    }
}