.button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 14px;

    width: fit-content;

    background: $color-default-black;
    border: 1px solid $color-default-black;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
    
    border-radius: 50px;

    font-family: $font-family--inter;
    font-weight: 600;
    line-height: 1.4;

    color: white;

    transition: color 0.3s, background 0.3s, opacity 0.3s, box-shadow 0.3s, border 0.3s;

    svg {
        flex-shrink: 0;
        margin-right: 8px;        
    }

    &:not([disabled]) {
        @include hover-or-touch {
            background: $color-dark-black;
            border-color: $color-dark-black;
    
            box-shadow: 0px 1px 2px 0px #1018280D;
        }
    
        &:focus,
        &:active,
        &:hover:focus,
        &:hover:active {
            background: $color-default-black;
            border: 1px solid $color-default-black;
            box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05), 0px 0px 0px 4px $color-dark-cold;
        }
    }
    
    &[disabled] {
        background: $color-magnolia;
        border: 1px solid $color-magnolia;
        
        color: rgba(21, 29, 63, 0.2);

        cursor: not-allowed;
        // pointer-events: none;
    }

    &.button--sm {
        min-height: 36px;

        font-size: 14px;
    }
    &.button--md {
        padding: 10px 16px;
        min-height: 40px;
    }
    &.button--lg {
        padding: 10px 18px;
        min-height: 44px;

        font-size: 16px;
        line-height: 1.5;
    }
    &.button--xl {
        padding: 11px 20px;
        
        min-height: 48px;

        font-size: 16px;
        line-height: 1.5;
    }
    &.button--2xl {
        padding: 16px 28px;
        min-height: 60px;

        font-family: $font-family--unbounded;
        font-weight: 500;
        font-size: 18px;
        line-height: 1.6;

        svg {
            width: 24px;
            height: 24px;
            margin-right: 12px;        
        }
    }

    &.button--cold {
        background: $color-cold;
        border: 1px solid $color-cold;

        color: $color-default-black;

        &:not([disabled]) {
            @include hover-or-touch {
                background: $color-dark-cold;
                border-color: $color-dark-cold;
            }
        
            &:focus,
            &:active,
            &:hover:focus,
            &:hover:active {
                background: $color-cold;
                border: 1px solid $color-cold;
                
                box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05), 0px 0px 0px 4px $color-dark-cold;
            }
        }
        
        &[disabled] {
            background: $color-magnolia;
            border: 1px solid $color-magnolia;
            
            color: rgba(21, 29, 63, 0.2);
    
            cursor: not-allowed;
            // pointer-events: none;
        }
    }
}