html,
body {
	max-width: 100vw;
	overflow-x: hidden;
	
	&.overflow-hidden {
		overflow: hidden;
	}
}

main {
	padding-top: 96px;

    @include vp--1023 {
		padding-top: 57px;
	}
}
section {
	padding: 24px 0;

    // @include vp--767 {
	// 	padding: 12px 0;
	// }
}
.visually-hidden {
	position: fixed !important;
	clip-path: inset(100%) !important;
	overflow: hidden !important;
	margin: -1px !important;
	border: 0 !important;
	padding: 0 !important;
	width: 1px !important;
	height: 1px !important;
	white-space: nowrap !important;
	clip: rect(0 0 0 0) !important;
	opacity: 0 !important;
	pointer-events: none !important;
	z-index: -1 !important;
}
.text {
	&--500 {
		font-weight: 500;
	}
	&--hidden {
		display: none;
	}
}
.color {
	&--gray {
		color: #151D3F66;
	}
}
.desktop-hidden {
	@include vpm--1024 {
		display: none;
	}
}

.pretitle {
	font-size: 16px;

	color: $color-dark-gray;
    z-index: 1;
}

.title {
	font-weight: 500;
	font-size: 40px;

	color: $color-default-black;
    z-index: 1;

    @include vp--1439 {
		font-size: 32px;
	}
    @include vp--1279 {
		font-size: 24px;
	}
}
.h3 {
	font-family: $font-family--unbounded;
	font-weight: 500;
	font-size: 18px;
	line-height: 1.2;
}
.show-more-btn {
	display: inline-flex;
	align-items: center;

	font-weight: 500;
	font-size: 16px;
	
	svg {
		margin-left: 4px;
	}
}
