.pagination-select {
    position: relative;
    padding: 4px 0;
    
    border: 1px solid transparent;
    border-radius: 8px;
    transition: border 0.3s, background 0.3s;

    @include hover-or-touch {
        border-color: $color-light-gray;

        .pagination-select__list {
            border-color: $color-light-gray;
        }
    }
    
    &.is-open {
        border-bottom: unset;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;

        .pagination-select__trigger-icon {
            svg {
                transform: rotate(180deg);
            }
        }

        .pagination-select__list {
            border-top-left-radius: 0;
            border-top-right-radius: 0;


            opacity: 1;
            pointer-events: unset;
        }

        @include hover-or-touch {
            background-color: white;

            .pagination-select__list {
                background-color: white;
            }
        }
    }
}
.pagination-select__trigger {
    display: flex;
    align-items: center;
    padding: 6px 14px;

    cursor: pointer;
}
.pagination-select__trigger-title {

}
.pagination-select__trigger-icon {
    margin-left: 8px;
    width: 16px;
    height: 16px;
    
    svg {
        flex-shrink: 0;

        transition: transform 0.3s;
    }
}
.pagination-select__list {
    position: absolute;
    bottom: 4px;
    left: -1px;
    width: calc(100% + 2px);

    max-height: 76px;
    
    background-color: white;
    border-radius: 8px;
    
    overflow-y: auto;
    opacity: 0;
    pointer-events: none;
    border: 1px solid transparent;
    border-top: unset;
    
    transform: translate3d(0, 100%, 0);
    transition: opacity 0.3s, background 0.3s, border 0.3s;
    z-index: 2;

    &::-webkit-scrollbar {
        width: 4px;
        border-radius: 100px;
    }

    &::-webkit-scrollbar-track {
        background: transparent;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $color-gray;
        border-radius: 20px;
        border: 3px solid $color-gray;
    }
}
.pagination-select__item {
    padding: 6px 14px;
    cursor: pointer;
    transition: color 0.3s;

    @include hover-or-touch {
        color: #151D3F4D;
    }
}