.header {
    position: fixed;
    top: 0;
    left: 50%;

    padding-top: 24px;
    width: 100%;
    transform: translate3d(-50%, 0, 0);
    background-color: white;

    transition: padding-top 0.3s;
    z-index: 3;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;

        height: calc(100 * var(--vh, 1vh));
        width: 100vw;

        pointer-events: none;
        opacity: 0;

        background-color: rgba(black, 0.25);
        transition: opacity 0.3s;
        z-index: -1;
    }

    &.header--fit {
        padding: 0;

        .header__nav-menu {
            top: -12px;
        }
    }

    &.is-open {
        &::before {
            opacity: 1;
            pointer-events: unset;
        }
        .header__menu-btn {
            span {
                opacity: 0;
                pointer-events: none;
            }
            &::before {
                transform: translate3d(-50%, -50%, 0) rotate(45deg);
            }
            &::after {
                transform: translate3d(-50%, -50%, 0) rotate(-45deg);
            }
        }
        .header__nav-menu {
            transform: translate3d(0, 0, 0);
            transition: transform 0.3s 0.15s;
        }
    }

    @include vp--1023 {
        padding-top: 0;
    }
}
.header__container {
    position: relative;
    display: flex;
    align-items: center;
    padding-top: 12px;
    padding-bottom: 12px;
}
.header__logo {
    flex-shrink: 0;
    width: 180px;
    height: 33px;
}
.header__nav {
    position: absolute;
    top: 50%;
    left: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: fit-content;
    transform: translate3d(-50%, -50%, 0);

    @include vp--1023 {
        position: unset;
        top: unset;
        left: unset;
        margin-left: auto;
        transform: translate3d(0, 0, 0);
    }
}
.header__menu-btn {
    position: relative;
    flex-shrink: 0;
    width: 32px;
    height: 32px;
    z-index: 1;

    span,
    &::before,
    &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 2px;
        border-radius: 200px;
        background-color: $color-default-black;
        transform: translate3d(-50%, -50%, 0);

        pointer-events: none;
        
        transition: opacity 0.3s, transform 0.3s;
    }
    &::before {
        transform: translate3d(-50%, calc(-50% - 10px), 0);
    }
    &::after {
        transform: translate3d(-50%, calc(-50% + 10px), 0);
    }
}
.header__nav-menu {
    display: flex;
    align-items: center;

    @include vp--1023 {
        position: fixed;
        top: -12px;
        right: -24px;

        padding: 32px;

        width: calc(50vw + 24px);
        height: calc(100 * var(--vh, 1vh));
        background: white;

        box-shadow: -1px 1px 5px 1px #ECF0FF;
        // box-shadow: -10px 1px 10px 1px red;

        transform: translate3d(calc(100% + 48px), 0, 0);
        transition: transform 0.3s 0.15s;
    }

    @include vp--767 {
        right: -12px;
        // align-items: flex-start;
        justify-content: flex-start;
        padding: 64px 16px;
        width: calc(80vw + 12px);
        overflow-y: auto;

        -ms-overflow-style: none;
        scrollbar-width: none;

        &::-webkit-scrollbar {
            display: none;
        }
    }
    @include vp--374 {
        width: 100vw;
    }
}
.header__contact-us {
    margin-left: auto;

    @include vp--1023 {
        display: none;
    }
}