.services {
    position: relative;
}
.services__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.services__pretitle {
    margin-bottom: 8px;
    text-align: center;
}
.services__title {
    text-align: center;
}

.services__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;

    margin-top: 32px;

    width: 100%;

    @include vp--767 {
        grid-template-columns: 1fr;

    }
}
.services__item {

}
