.footer {
    padding-bottom: 46px;
}
.footer__container {
    display: flex;
    flex-direction: column;
}
.footer__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 0;

    @include vp--767 {
        flex-direction: column;
    }
}
.footer__logo {
    width: fit-content;

    font-weight: 400;
    font-size: 20px;

    @include vp--767 {
        margin-bottom: 20px;
    }
}
.footer__menu {
    display: flex;
    align-items: center;
}
.footer__menu-item {
    & + & {
        margin-left: 26px;
    }
}
.footer__menu-item-link {
    display: flex;
    align-items: center;
    text-align: center;

    font-family: $font-family--inter;
    font-size: 16px;
}
.footer__bottom {
    position: relative;

    display: flex;
    align-items: center;
    margin-top: 8px;

    font-family: $font-family--inter;
    font-size: 16px;

    @include vp--767 {
        flex-direction: column;
    }
}
.footer__copy {
    position: absolute;
    top: 50%;
    left: 50%;

    font-size: 14px;

    transform: translate3d(-50%, -50%, 0);

    @include vp--767 {
        position: relative;
        top: unset;
        left: unset;
        order: 1;
        transform: translate3d(0, 0, 0);
    }
}
.footer__bottom-policy {
    margin-left: auto;
    text-decoration: underline;

    @include vp--767 {
        margin: 0;
        margin-bottom: 20px;
    }
}