.hero {
    padding-top: 0;
    height: calc((100 * var(--vh, 1vh)) - 96px - 24px);
    min-height: 750px;

    @media (max-height: 850px) {
        min-height: 675px;
    }

    @include vp--1023 {
        padding-top: 12px;
    }

    @include vp--767 {
        height: auto !important;
        min-height: unset;
    }
}

.hero__container {
    position: relative;
    height: 100%;
    z-index: 0;
}
.hero__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
.hero__content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding: 64px 32px;

    width: 100%;
    height: 100%;

    border-radius: 40px;
    overflow: hidden;

    @media (max-height: 850px) {
        justify-content: flex-start;
    }

    @include vp--767 {
        padding: 32px;
        padding-bottom: 200px;
        border-radius: 24px;
    }
    @include vp--575 {
        padding: 120px 16px;
    }
}
.hero__title {
    font-weight: 600;
    font-size: 48px;
    text-align: center;

    @include vp--1023 {
        font-size: 36px;
    }
    @include vp--767 {
        font-size: 24px;
    }
}
.hero__subtitle {
    margin-top: 20px;

    text-align: center;

    font-size: 24px;

    @include vp--1023 {
        margin-top: 12px;
        font-size: 18px;
    }
    @include vp--767 {
        font-size: 16px;
    }
}
.hero__search {
    display: flex;
    flex-direction: column;
    margin-top: 48px;

    @include vp--1023 {
        margin-top: 24px;
    }
}
.hero__sources {
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 32px;
    padding-top: 80px;
    width: 425px;
    height: 176px;
    z-index: 1;

    @include vp--575 {
        padding: 16px;
        width: 100%;
        justify-content: center;
        height: auto;
        border-radius: 32px 32px 0 0;
        background: white;
    }
}
.hero__sources-bg {
    position: absolute;
    bottom: 0;
    right: -1px;
    width: 100%;
    height: 100%;
    z-index: -1;

    img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
    @include vp--575 {
        display: none;
    }
}
.hero__sources-text {
    margin-right: 15px;
    width: 155px;

    font-weight: 300;
    font-size: 14px;

    @include vp--575 {
        font-size: 12px;
    }
}
.hero__sources-pics {
    display: flex;
    align-items: center;
    justify-content: center;
}
.hero__sources-pics-item {
    flex-shrink: 0;

    border: 1.69px solid #FFFFFF;
    border-radius: 300px;
    width: 54px;
    height: 54px;

    & + & {
        margin-left: -20px;
    }

    img {
        flex-shrink: 0;

        width: 100%;
        height: 100%;
        object-fit: contain;
    }

    @include vp--575 {
        width: 48px;
        height: 48px;
    }
}