.indicator {
    display: flex;
    flex-direction: column;

    padding: 20px;

    height: 100%;

    background: #FFFFFF;
    border: 1px solid rgba(21, 29, 63, 0.1);
    border-radius: 20px;
 
    font-family: $font-family--unbounded;

    @include vp--374 {
        padding: 12px;
    }
}
.indicator__title {
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 16px;
}
.indicator__years {
    margin-top: auto;
    columns: 2;
    gap: 20px;

    @include vp--374 {
        gap: 8px;
    }
}
.indicator__years-item {
    display: flex;
    align-items: center;
    padding: 4px 0;
}
.indicator__years-item-year {
    width: 52px;

    font-size: 14px;
    line-height: 1.2;

    color: $color-dark-gray;

    @include vp--374 {
        width: 40px;
        font-size: 12px;
    }
}
.indicator__years-item-number {
    margin-left: 20px;

    font-weight: 500;

    @include vp--374 {
        margin-left: 8px;
        font-size: 14px;
    }
}
.indicator__chart {
    margin-top: 20px;
    padding-top: 24px;

    border-top: 1px solid $color-light-gray;

    // .apexcharts-legend {
    //     display: none;
    // }
}