// Unbounded
@font-face {
    font-family: 'Unbounded';
    src: local('Unbounded Regular'), local('Unbounded-Regular'),
        url('../fonts/unbounded-regular.woff2') format('woff2'),
        url('../fonts/unbounded-regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

