//     -  
//---------------------------------
@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin hover-or-touch {
  @media (hover: hover) {
    &:hover {
      @content;
    }
  }

  @media (hover: none) {
    &:active {
      @content;
    }
  }
}


//   
//---------------------------------
@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

