.menu {
    display: flex;
    align-items: center;

    @include vp--1023 {
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }
}
.menu__item {
    & + & {
        margin-left: 32px;
    }
    @include vp--1199 {
        & + & {
            margin-left: 16px;
        }
    }
    @include vp--1023 {
        & + & {
            margin: 0;
            margin-top: 16px;
        }
    }
}
.menu__item-link {
    position: relative;
    display: flex;
    align-items: center;
    text-align: center;
    padding: 10px 0;

    font-family: $font-family--inter;
    font-size: 16px;
    line-height: 1.4;

    color: $color-default-black;
    white-space: nowrap;
    opacity: 0.7;

    transition: color 0.3s, opacity 0.3s;

    &::before {
        content: "";
        position: absolute;
        display: block;
        width: 32px;
        height: 2px;
        bottom: 2px;
        left: 0;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;

        background-color: $color-default-black;
        transform: scaleX(0);
        transform-origin: top left;
        transition: transform 0.3s ease;
    }

    @include hover-or-touch {
        opacity: 1;

        &::before {
            transform: scaleX(1);
        }
    }

    &.is-active {
        font-family: $font-family--unbounded;
        pointer-events: none;
        // font-weight: 600;
        // opacity: 1;

        // &::before {
            // transform: scaleX(1);
        // }
    }
    @include vp--1023 {
        padding: 6px 0;

        font-family: $font-family--unbounded;
        font-weight: 600;
        font-size: 18px;

        white-space: unset;
        text-align: left;
        text-transform: uppercase;
    }
    @include vp--767 {
        font-size: 16px;
    }
    @include vp--374 {
        font-size: 16px;
    }
}
.menu__item--btn {
    @include vp--1023 {
        width: 100%;
        margin-top: 32px !important;
    }
}
.menu__contact-us {
    @include vp--1023 {
        width: 100%;
    }
}