.market-card {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 32px 24px;

    max-width: 562px;

    border-radius: 24px;
    background-color: #73A2C21A;

    transition: background 0.3s;

    ul {
        list-style: none;
    }
    @include hover-or-touch {
        background-color: #73A2C233;
    }
    
    &.market-card--negative {
        background-color: #EC9C901A;

        @include hover-or-touch {
            background-color: #EC9C9033;
        }
        .market-card__state-item-icon,
        .market-card__state-item-text {
            color: #F06F5B;
        }
    }
    &.market-card--positive {
        background-color: #3DB4931A;

        @include hover-or-touch {
            background-color: #3DB49333;
        }
        .market-card__state-item-icon,
        .market-card__state-item-text {
            color: #3DB493;
        }
    }
}
.market-card__icon {
    position: absolute;
    top: 32px;
    right: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    
    svg {
        flex-shrink: 0;
        width: 100%;
        height: 100%;
    }
}
.market-card__title {
    width: 100%;
    max-width: calc(100% - 48px);
    font-weight: 500;

    span {
        margin-left: 5px;
    }

    @include vp--767 {
        font-size: 14px;
    }
}

.market-card__state {
    // display: flex;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 8px;
    margin-top: 24px;

    @include vp--767 {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        grid-template-columns: unset;
    }
}
.market-card__state-item {
    display: flex;
    align-items: center;
    padding: 8px;
    background-color: white;
    border-radius: 12px;

    // &:not(:last-child) {
    //     margin-right: 8px;
    // }
    @include vp--767 {
        width: 100%;
    }
}
.market-card__state-item-icon {
    margin-right: 10px;

    width: 24px;
    height: 24px;
    
    color: #81919C;
}
.market-card__state-item-title {
    margin-right: 10px;
    
    font-size: 14px;
    line-height: 1.85;

    @include vp--767 {
        font-size: 12px;
    }
}
.market-card__state-item-text {
    font-weight: 500;
    font-size: 14px;

    color: #81919C;

    @include vp--767 {
        font-size: 12px;
    }
}
.market-card__list {
    margin-top: 24px;
    display: flex;
    flex-direction: column;
}
.market-card__item {
    display: flex;
    align-items: center;
    justify-content: space-between;

    & + & {
        margin-top: 12px;
    }

    @include vp--767 {
        flex-direction: column;
    }

    @include vp--575 {
        & + & {
            margin-top: 8px;
        }
    }
}
.market-card__item-name {
    font-size: 14px;
    line-height: 1.85;

    color: rgba(21, 29, 63, 0.4);

    @include vp--767 {
        align-self: flex-start;
        font-size: 12px;
    }
}
.market-card__item-number {
    margin-left: 16px;

    white-space: nowrap;
    font-weight: 500;

    font-size: 16px;
    line-height: 1.5;

    @include vp--767 {
        align-self: flex-end;
        font-size: 14px;
    }
}