.custom-field {
    position: relative;
    user-select: none;

    &.not-valid,
    &.is-empty {
        .custom-field__input {
            border-color: rgba($color-error, 0.5);
        }
    }
    &.not-valid:not(.is-empty) {
        .error {
            opacity: 1;
        }
    }
    &.is-empty,
    &.is-empty.not-valid {
        .error {
            opacity: 0;
        }
        .empty {
            opacity: 1;
        }
    }

    &.custom-field--checkbox {
        display: flex;
        align-items: center;

        cursor: pointer;

        .error,
        .empty {
            bottom: 0;
        }

        input {
            &:checked {
                & ~ .custom-field__icon {
                    background: linear-gradient(0deg, #10CC97, #10CC97), #FFFFFF;
                    border-color: $color-gray;

                    svg {
                        opacity: 1;
                    }
                }
            }
        }
        &.not-valid,
        &.is-empty {
            .custom-field__icon {
                border-color: $color-error;
            }
            .custom-field__text {
                color: $color-error;
            }
        }
    }
    &.custom-field--textarea {
        .custom-field__input {
            height: 114px;
        }
    }
}
.custom-field__input {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 16px 20px;

    width: 340px;
    height: 64px;

    background: rgba(232, 241, 255, 0.3);
    border: 1px solid $color-gray;
    border-radius: 16px;
    outline: 4px solid transparent;

    // box-shadow: 0px 1px 2px transparent;

    font-family: $font-family--inter;
    font-size: 16px;
    line-height: 1.4;

    transition: border 0.3s, color 0.3s, background 0.3s, outline 0.3s, box-shadow 0.3s;

    &::placeholder {
        color: rgba(21, 29, 63, 0.3);
    }

    &[disabled] {
        color: #151D3F4D;
    }

    &:focus {
        outline-color: #E3EEFF;
        // box-shadow: 0px 1px 2px 0px #1018280D;
    }

    @include hover-or-touch {
        box-shadow: 0px 1px 2px 0px #10182810;
    }

    @include vp--575 {
        border-radius: 12px;
    }
}
.custom-field__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    
    margin-right: 12px;

    width: 16px;
    height: 16px;

    background: transparent;
    border: 1px solid $color-gray;
    border-radius: 4px;

    transition: border 0.3s, background 0.3s;

    svg {
        flex-shrink: 0;
        
        width: 100%;
        height: 100%;
        
        color: white;
        opacity: 0;

        transition: opacity 0.3s;
    }
}
.custom-field__text {
    font-family: $font-family--inter;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.4;

    color: $color-default-black;
    transition: color 0.3s;

    a {
        text-decoration: underline;
    }
}
.error,
.empty {
    position: absolute;
    bottom: -2px;
    left: 0;

    font-family: $font-family--inter;
    line-height: 1.4;
    color: $color-error;

    opacity: 0;
    pointer-events: none;

    transform: translate3d(0, 100%, 0);
    transition: opacity 0.3s;
}