.chart {
    display: flex;
    justify-content: space-between;
}
.chart__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    width: calc((100% - 24px * 4) / 5);

    font-family: $font-family--unbounded;
    font-size: 14px;
    line-height: 1.2;
    text-align: center;

    &.chart__item--top {
        .chart__item-bar {
            span {
                background-color: #5586E7;
            }
        }
    }

    @include vp--1439 {
        width: calc((100% - 12px * 4) / 5);
        font-size: 12px;
    }
    @include vp--1279 {
        width: calc((100% - 8px * 4) / 5);
        font-size: 14px;
    }
    @include vp--1023 {
        font-size: 12px;
    }
    @include vp--767 {
        font-size: 14px;
        width: calc((100% - 24px * 4) / 5);
    }
    @include vp--575 {
        width: calc((100% - 12px * 4) / 5);
        font-size: 12px;
    }
    @include vp--374 {
        width: calc((100% - 8px * 4) / 5);
        font-size: 10px;
    }
}
.chart__item-value {
    margin-bottom: 8px;
}
.chart__item-bar {
    position: relative;

    width: 100%;
    height: 112px;

    overflow: hidden;
    border-radius: 10px;

    background-image: repeating-linear-gradient(
        45deg, /*    */
        #F3F5F9, /*     */
        #F3F5F9 2px, /*   */
        transparent 2px, /*    */
        transparent 8px /*     ( + ) */
      );

    span {
        position: absolute;
        bottom: 0;
        left: 0;

        display: flex;
        flex-direction: column;
        align-items: flex-start;

        width: 100%;

        background: #EAF2FF;
        border-radius: 10px;

        transition: background 0.3s, height 0.5s ease-in-out;
    }
}
.chart__item-year {
    margin-top: 8px;

    color: $color-dark-gray-2;
}