.bests {
}
.bests__container {
}
.bests__title {
    text-align: center;
}
.bests__list {
    display: grid;
    grid-template-columns: repeat(auto-fill, 328px);
    justify-content: center;
    gap: 20px;
    margin-top: 32px;

    list-style-type: none; 
	counter-reset: num;

    @include vp--767 {
        grid-template-columns: repeat(auto-fill, 296px);
        gap: 12px;
    }
}
.bests__item {
    &::before {
        content: counter(num) '.'; 
        counter-increment: num;

        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 32px;

        width: 32px;
        height: 32px;

        background: #EAF2FF;
        border-radius: 100px;

        font-size: 18px;
        align-items: center;

    }
    @include vp--767 {
        &::before {
            margin-bottom: 24px;
            font-size: 16px;
        }
    }
}
