.pagination {
    display: flex;
    align-items: center;
    justify-content: space-between;

    font-family: $font-family--inter;
    font-size: 14px;
    line-height: 1.2;

    color: $color-default-black;

    &.pagination--grid {
        display: grid;
        grid-template-columns: 1fr 3fr 1fr;
        gap: 16px;
    }

    @include vp--767 {
        &.pagination--grid {
            grid-template-columns: 1fr 1fr;
        }
    }
    @include vp--374 {
        &.pagination--grid {
            gap: 12px;
        }
    }
}
.pagination__info {
    @include vp--767 {
        grid-row: 1 / 2;
    }
    @include vp--575 {
        font-size: 12px;
    }
}
.pagination__navigation {
    display: flex;
    align-items: center;
    justify-content: center;

    @include vp--767 {
        grid-column: 1 / -1;
    }
}
.pagination__btn {
    display: flex;
    align-items: center;
    padding: 10px 12px;
    gap: 4px;

    background-color: transparent;
    border: none;
    outline: none;

    color: $color-default-black;

    svg {
        flex-shrink: 0;

        width: 16px;
        height: 16px;
        color: $color-default-black;

        opacity: 0.3;
        transition: opacity 0.3s;
    }

    &[disabled] {
        color: #151D3F4D;
        pointer-events: none;
    }

    @include hover-or-touch {
        svg {
            opacity: 1;
        }
    }
    @include vp--575 {
        padding: 4px 6px;
        font-size: 12px;
    }
}
.pagination__btn--prev {
    margin-right: 6px;

    svg {
        transform: rotate(90deg);
    }
}
.pagination__btn--next {
    margin-left: 6px;

    svg {
        transform: rotate(-90deg);
    }
}
.pagination__list {
    display: flex;
    align-items: center;
}
.pagination__list-item {
    & + & {
        margin-left: 6px;
    }
    @include vp--575 {
        & + & {
            margin-left: 3px;
        }
    }
}
.pagination__list-item-link {
    display: flex;
    align-items: center;
    justify-content: center;
    
    width: 36px;
    height: 36px;

    border-radius: 10px;
    border: 1px solid transparent;
    transition: border 0.3s;

    &.is-active {
        background-color: $color-default-black;
        color: white;
        border-radius: 200px;
    }

    &:active {
        border-color: $color-light-gray;
    }
    @include hover-or-touch {
        border-color: $color-light-gray;
    }

    @include vp--374 {
        width: 24px;
        height: 24px;

        font-size: 12px;
    }
}
.pagination__select {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    @include vp--767 {
        grid-row: 1 / 2;
    }
    @include vp--374 {
        font-size: 12px;
    }
}
.pagination__select-text {
    margin-right: 8px;

    @include vp--1023 {
        display: none;
    }
}