.search-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}
.search-form__line {
    display: flex;
    align-items: center;
    justify-content: center;

    @include vp--1023 {
        display: grid;
        grid-template-columns: 1fr 165px;
        gap: 12px;

        .custom-select__trigger {
            padding: 14px 20px;
            border-radius: 12px;
        }
    }
    @include vp--767 {
        grid-template-columns: 1fr;
    }
}
.search-form__categories {
    width: 100%;
    max-width: 750px;

    @include vp--1023 {
        grid-column: 1 / -1;
        max-width: 100%;
    }
}
.search-form__cities {
    margin-left: 20px;
    
    width: 100%;
    max-width: 287px;

    @include vp--1023 {
        max-width: 100%;
        margin: 0;
    }
}
.search-form__submit.button {
    margin-left: 20px;
    border-radius: 16px;

    height: 100%;

    @include vp--1023 {
        margin: 0;
        padding: 14px 28px;
        width: 100%;
        min-height: 54px;
        border-radius: 12px;
        font-size: 16px;
    }
}
.search-form__tags {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-self: center;
    margin-top: 24px;
    gap: 12px;
    max-width: 900px;

    .tags__item-link {
        font-weight: 500;
        font-size: 14px;
    }

    @include vp--1023 {
        gap: 8px;
    }

    @include vp--575 {
        gap: 4px;
        .tags__item-link {
            padding: 0 8px;
            height: 32px;
            font-size: 12px;
        }
    }
}