.callback-form {
}
.callback-form__container {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.callback-form__title {
}
.callback-form__form {
    display: grid;
    grid-template-columns: 1.5fr 1fr;
    gap: 20px;
    margin-top: 32px;

    width: 100%;

    @include vp--1023 {
        grid-template-columns: 1fr;
    }
}
.callback-form__form-content {
    padding: 48px;

    border: 1px solid #E8F1FF;
    border-radius: 32px;
    backdrop-filter: blur(90px);

    &.is-sended {
        & ~ .callback-form__form-aside {
            .callback-form__form-aside-sended {
                opacity: 1;
            }
        }
    }

    @include vp--1023 {
        padding: 24px;
    }
    @include vp--767 {
        border-radius: 24px;
    }
}
.callback-form__form-aside {
    position: relative;
    display: flex;
    flex-direction: column;

    padding: 48px;

    background: #E8F1FF;
    border: 1px solid #E8F1FF;
    border-radius: 32px;

    overflow: hidden;

    img {
        position: absolute;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
    }

    @include vp--767 {
        padding: 24px;
        border-radius: 24px;
    }
}
.callback-form__form-aside-sended {
    display: flex;
    flex-direction: column;

    padding: 24px;

    max-width: 516px;
    
    background: rgba(255, 255, 255, 0.4);
    backdrop-filter: blur(25px);
    
    border-radius: 24px;

    font-size: 14px;

    opacity: 0;
    pointer-events: none;

    transition: opacity 0.3s;

    svg {
        flex-shrink: 0;
        margin-bottom: 12px;

        width: 48px;
        height: 48px;
    }

    @include vp--767 {
        svg {
            width: 36px;
            height: 36px;
        }
    }
}