.markets {

}
.markets__container {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.markets__title {
    margin-bottom: 20px;
}
.markets__header {
    display: flex;
    align-items: center;

    width: 100%;

    .custom-select__trigger {
        padding: 11px 20px;
    }

    @include vp--767 {
        display: grid;
        grid-template-columns: 1fr 152px;
        gap: 12px;
    }
    @include vp--374 {
        grid-template-columns: 1fr;
    }
}
.markets__header-cities {
    width: 100%;
    max-width: 340px;
    margin-right: 12px;

    @include vp--767 {
        grid-row: 1 / 2;
    }
    @include vp--374 {
        grid-row: unset;
    }
}
.markets__header-categories {
    width: 100%;
    max-width: 625px;
    margin-right: 12px;

    @include vp--767 {
        grid-column: 1 / -1;
        max-width: 100%;
    }
}
.markets__header-link {
    margin-left: auto;
    height: 48px;

    white-space: nowrap;

    @include vp--767 {
        grid-row: 1 / 2;
    }
    @include vp--374 {
        grid-row: unset;
        width: 100%;
        border-radius: 16px;
    }
}
.markets__list {
    display: grid;
    grid-template-columns: repeat(auto-fill, 562px);
    justify-content: center;
    gap: 16px;
    margin-top: 20px;

    width: 100%;
    max-width: 100%;
    
    @include vp--575 {
        grid-template-columns: 1fr;
    }
}
.markets__item {
    display: flex;
    
    width: 100%;
}
.markets__item-card {
    display: flex;

    width: 100%;
}

.markets__pagination {
    margin-top: 12px;

    width: 100%;
    min-height: 76px;
}