.tags {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    
    // padding: 20px;
    gap: 8px;

    // background: #BBA1DD;
    // border-radius: 20px;
}

.tags__item {

}

.tags__item-link {
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 0px 16px;

    height: 40px;

    font-family: $font-family--inter;
    background: rgba(255, 255, 255, 0.4);
    backdrop-filter: blur(20px);
    border-radius: 40px;

    cursor: pointer;
    transition: background 0.3s;

    @include hover-or-touch {
        background: rgba(255, 255, 255, 0.6);
    }
}
