.service {
    position: relative;

    display: flex;
    flex-direction: column;

    padding: 32px;

    border: 1px solid #E8F1FF;
    backdrop-filter: blur(45px);

    border-radius: 32px;
    overflow: hidden;
    z-index: 0;

    transition: background 0.3s;

    @include hover-or-touch {
        background: rgba(234, 242, 255, 0.3);
    }

    @include vp--1023 {
        padding: 24px;
        border-radius: 24px;
    }
}
.service__header {
    display: flex;
    align-items: center;
    justify-content: space-between;

    padding-top: 2px;
    padding-bottom: 34px;
    border-bottom: 1px solid $color-light-gray;

    & + .service__list {
        margin-top: 32px;
    }

    @include vp--1023 {
        align-items: flex-start;
        flex-direction: column;

        & + .service__list {
            margin-top: 24px;
        }
    }
}
.service__title {
    text-align: left;
    font-weight: 500;
    font-size: 20px;

    @include vp--1023 {
        font-size: 18px;
    }
}
.service__price {
    position: relative;
    
    margin-left: 24px;
    
    font-weight: 500;
    font-size: 24px;
    line-height: 1.25;

    text-align: right;
    white-space: nowrap;

    @include vp--1023 {
        margin-left: auto;
        margin-top: 12px;

        font-size: 20px;
    }
}
.service__price-old {
    position: absolute;
    bottom: -5px;
    right: 0;

    font-size: 16px;

    color: $color-error;

    transform: translate3d(0, 100%, 0);

    &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;

        height: 1px;
        width: calc(100% + 6px);
        background-color: $color-error;

        transform: translate3d(-50%, -50%, 0);
    }

    @include vp--1023 {
        font-size: 14px;
    }
}
.service__list {
    margin-bottom: 94px;
    max-width: 590px;

    @include vp--1023 {
        margin-bottom: 48px;
    }
}
.service__item {
    display: flex;
    align-items: center;

    svg {
        margin-right: 8px;
        flex-shrink: 0;
        width: 24px;
        height: 24px;
        color: $color-green;
    }

    & + & {
        margin-top: 24px;
    }

    @include vp--1023 {
        & + & {
            margin-top: 16px;
        }
    }
    @include vp--767 {
        font-size: 14px;
    }
}
.service__link {
    margin-top: auto;
}
.service__bg {
    position: absolute;
    bottom: 0;
    right: 0;

    z-index: -1;

    @include vp--767 {
        width: 75%;
    }
}