.form {
    display: flex;
    flex-direction: column;
}
.form__row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 32px;

    & + * {
        margin-top: 24px;
    }

    @include vp--1023 {
        gap: 24px;
    }
    @include vp--575 {
        grid-template-columns: 1fr;
    }
}
.form__field {
    width: 100%;
}
.form__field-title {
    margin-bottom: 12px;

    font-family: $font-family--inter;
    font-size: 16px;
    line-height: 1;
    letter-spacing: -0.02em;

    opacity: 0.6;
}
.custom-field {
    .custom-field__input {
        width: 100%;
    }
}
.custom-field__input {
}
.form__footer {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    margin-top: 24px;
    
    .custom-field--checkbox {
        .custom-field__icon {
            flex-shrink: 0;

            width: 24px;
            height: 24px;
        }
        .custom-field__text {
            max-width: 450px;
        }
    }

    @include vp--575 {
        flex-direction: column;
    }
}
.form__submit {
    margin-left: 32px;

    flex-shrink: 0;

    @include vp--575 {
       margin: 0;
       margin-top: 24px;

       width: 100%;
    }
}
